/* eslint-disable no-unused-vars */
<template>
  <v-col cols="12" md="6">
    <v-menu
      ref="menu"
      v-model="menu"
      :close-on-content-click="false"
      transition="scale-transition"
      left
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          v-model="start"
          :placeholder="placeholder"
          hint="MM/DD/YYYY format"
          persistent-hint
          prepend-icon="event"
          @blur="passDate"
          v-on="on"
          filled
          required
          :rules="rules"
        ></v-text-field>
      </template>
      <v-date-picker
        v-model="date"
        no-title
        @input="menu = false"
      ></v-date-picker>
    </v-menu>
  </v-col>
</template>

<script>
export default {
  name: "DateField",
  props: ["placeholder"],
  data: vm => ({
    date: new Date().toISOString().substr(0, 10),
    start: vm.formatDate(new Date().toISOString().substr(0, 10)),
    menu: false,
    rules: [v => !!v || "This is required"]
  }),
  watch: {
    date: {
      handler: function() {
        this.start = this.formatDate(this.date);
        this.$emit("dates", this.date);
      }
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${month}/${day}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;
      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
    passDate() {
      this.date = this.parseDate(this.start);
    }
  },
  mounted() {
    this.$emit("dates", this.date);
  }
};
</script>
