<template lang="pug">
nav
  v-navigation-drawer(v-model="drawer" app)
    v-list(dense)
      v-list-item(link to="/")
        v-list-item-action
          v-icon mdi-home
        v-list-item-content
          v-list-item-title Home
      v-list-item(link to="/calendar")
        v-list-item-action
          v-icon mdi-calendar
        v-list-item-content
          v-list-item-title Calendar
      v-list-item(v-if="!isLoggedIn" link to="/login")
        v-list-item-action
          v-icon mdi-login
        v-list-item-content
          v-list-item-title Login
      v-list-item(v-if="isLoggedIn" @click="logout")
        v-list-item-action
          v-icon mdi-logout
        v-list-item-content
          v-list-item-title Logout
  v-app-bar(app dark)
    v-app-bar-nav-icon(@click.stop="drawer = !drawer")
    v-toolbar-title Employee Manager
</template>

<script>
import firebase from "firebase/app";

export default {
  name: "Navbar",
  data() {
    return {
      drawer: false,
      isLoggedIn: false
    };
  },
  methods: {
    logout() {
      if (firebase.auth().currentUser) {
        firebase
          .auth()
          .signOut()
          .then(() => {
            alert("You have signed out successfully");
            this.$router.push("/login");
          });
      }
    }
  },
  mounted() {
    firebase.auth().onAuthStateChanged(user => {
      user ? this.isLoggedIn = true : this.isLoggedIn = false;
    })
  }
};
</script>

