<template lang="pug">
v-container(fluid)
  v-btn(class="addNewEmployee success" to="/add-employee") Add New Employee
  v-row
    v-col(cols="12")
      v-row(:align="alignment" class="d-flex justify-center" v-if="listView =='Card View'")
        v-card(v-for="employee in employees" :key="employee.id" class="employee")
          span(class="id") id: {{ employee.employee_id }}
          p dept: {{ employee.dept }}
          p first name: {{ employee.first_name }}
          p last name: {{ employee.last_name }}
          p position: {{ employee.position }}
          p wage per hr: {{ employee.wage }}
          p cashapp: 
            span(class="regular") {{ employee.cashapp }}
          p 
            span(class="regular") email: {{ employee.email }}
          p phone: {{ employee.phone }}
          v-btn(class="btn" depressed small color="warning" :to="{ name: 'edit-employee', params: { employee_id: employee.employee_id }}" )  Edit Employee
          v-btn(class="btn" depressed small color="error" @click="deleteEmployee(employee.employee_id)") Delete Employee
      v-row(:align="alignment" class="d-flex justify-center" v-else)
        v-card( v-for="employee in employees" :key="employee.id" class="list-employee")
          span(class="id") id: {{ employee.employee_id }}
          v-btn(class="btn" depressed small color="warning" :to="{ name: 'edit-employee', params: { employee_id: employee.employee_id }}" )  Edit
          v-btn(class="btn" depressed small color="error" @click="deleteEmployee(employee.employee_id)") Delete 
          p dept: {{ employee.dept }} | first name: {{ employee.first_name }} | last name: {{ employee.last_name }} | position: {{ employee.position }} | wage per hr: {{ employee.wage }} | cashapp: 
            span(class="regular") {{ employee.cashapp }} | Email: {{ employee.email }} | Phone: {{ employee.phone }}

</template>

<script>
import db from "./firebaseInit";

export default {
  name: "Employees",
  props: ["listView"],
  data() {
    return {
      employees: [],
      alignment: "center"
    };
  },
  created() {
    db.collection("employees")
      .orderBy("employee_id")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            first_name: doc.data().first_name,
            last_name: doc.data().last_name,
            dept: doc.data().dept,
            position: doc.data().position,
            wage: doc.data()["Wage per Hr"],
            cashapp: doc.data().Cashapp,
            email: doc.data().email,
            phone: doc.data().phone
          };
          this.employees.push(data);
        });
      });
  },
  methods: {
    deleteEmployee(id) {
      if (confirm("Are you sure you want to delete?")) {
        db.collection("employees")
          .where("employee_id", "==", id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref
                .delete()
                .then(() => {
                  this.updateEmployeeView();
                })
                .catch(error => console.log(error));
            });
          });
      }
    },
    updateEmployeeView() {
      db.collection("employees")
        .orderBy("employee_id")
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              id: doc.id,
              employee_id: doc.data().employee_id,
              first_name: doc.data().first_name,
              last_name: doc.data().last_name,
              dept: doc.data().dept,
              position: doc.data().position,
              wage: doc.data()["Wage per Hr"],
              cashapp: doc.data().Cashapp,
              email: doc.data().email,
              phone: doc.data().phone
            };
            this.employees.push(data);
          });
        });
    }
  },
  watch: {
    listView: {
      handler: function(value) {
        console.log(value);
        console.log(this.employees);
      }
    }
  }
};
</script>

