<template>
  <v-col cols="12" md="6">
    <v-menu
      ref="menu"
      v-model="menu"
      :close-on-content-click="false"
      :return-value.sync="time"
      transition="scale-transition"
      left
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          v-model="time"
          :label="label"
          prepend-icon="access_time"
          readonly
          v-on="on"
          filled
          required
          :rules="rules"
        ></v-text-field>
      </template>
      <v-time-picker
        v-if="menu"
        v-model="time"
        full-width
        @click:minute="$refs.menu.save(time)"
      ></v-time-picker>
    </v-menu>
  </v-col>
</template>

<script>
export default {
  name: "TimePicker",
  props: ["label"],
  data() {
    return {
      time: null,
      menu: false,
      rules: [v => !!v || "This is required"]
    };
  },
  watch: {
    time: {
      handler: function() {
        this.$emit("time", this.time);
      }
    }
  }
};
</script>

<style>
.center {
  right: 0;
  left: 0;
  margin: auto;
}
</style>
