<template lang="pug">
v-container(class="space")
    h1(class="text-center") Edit Employee
    v-form(@submit.prevent="updateEmployee")
      label ID:
      v-text-field(class="id" v-model="employee_id" disabled)
      label Dept:
      v-text-field(v-model="dept" required)
      label First Name:
      v-text-field(v-model="first_name" required)
      label Last Name:
      v-text-field(v-model="last_name" required)
      label Position:
      v-text-field(v-model="position" required)
      label Wage:
      v-text-field(v-model="wage" required)
      label Cashapp:
      v-text-field(v-model="cashapp" required)
      label Phone:
      v-text-field(v-model="phone")
      label Email:
      v-text-field(v-model="email")
      v-btn(type="submit" class="submit success") Submit
      v-btn(to="/" class="cancel warning") Cancel
</template>

<script>
import db from "@/components/firebaseInit";

export default {
  name: "EditEmployee",
  data() {
    return {
      employee_id: null,
      dept: null,
      first_name: null,
      last_name: null,
      position: null,
      wage: null,
      cashapp: null,
      phone: null,
      email: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id;
            vm.first_name = doc.data().first_name;
            vm.last_name = doc.data().last_name;
            vm.dept = doc.data().dept;
            vm.position = doc.data().position;
            vm.wage = doc.data()["Wage per Hr"];
            vm.cashapp = doc.data().Cashapp;
            vm.phone = doc.data().email;
            vm.email = doc.data().phone;
          });
        });
      })
      .catch(error => console.log(error));
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee_id = doc.data().employee_id;
            this.first_name = doc.data().first_name;
            this.last_nem = doc.data().last_name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
            this.wage = doc.data()["Wage per Hr"];
            this.cashapp = doc.data().Cashapp;
            this.phone = doc.data().email;
            this.email = doc.data().phone;
          });
        })
        .catch(error => console.log(error));
    },
    updateEmployee() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                employee_id: this.employee_id,
                dept: this.dept,
                first_name: this.first_name,
                last_name: this.last_name,
                position: this.position,
                ["Wage per Hr"]: this.wage,
                Cashapp: this.cashapp,
                phone: this.phone,
                email: this.email
              })
              .then(() => {
                this.$router.push("/");
              })
              .catch(error => console.log(error));
          });
        });
    }
  },
  watch: {
    $route: {
      handler: function() {
        this.fetchData();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.id {
  background: #ccc;
}
.submit {
  background: green;
  color: white;
  border-radius: 5px;
  margin: 5px;
}
</style>
