<template lang="pug">
  v-container(class="space")
    h1(class="text-center") Add Employee
    v-form(@submit.prevent="checkInputs")
      label ID:
      v-text-field(v-model="employee_id" :rules="rules" required)
      label Dept:
      v-text-field(v-model="dept" :rules="rules" required)
      label First Name:
      v-text-field(v-model="first_name" :rules="rules" required)
      label Last Name:
      v-text-field(v-model="last_name" :rules="rules" required)
      label Position:
      v-text-field(v-model="position" :rules="rules" required)
      label Wage:
      v-text-field(v-model="wage" :rules="rules" required)
      label Cashapp:
      v-text-field(v-model="cashapp" :rules="rules" required)
      label Email:
      v-text-field(v-model="email" :rules="rules" required)
      label Phone:
      v-text-field(v-model="phone" :rules="rules" required)
      v-btn(type="submit" class="submit success") Submit
      v-btn(to="/" class="cancel warning") Cancel
</template>

<script>
import db from "@/components/firebaseInit";

export default {
  name: "AddEmployee",
  data() {
    return {
      employee_id: null,
      dept: null,
      first_name: null,
      last_name: null,
      position: null,
      wage: null,
      cashapp: null,
      email: null,
      phone: null,
      rules: [v => !!v || "This is required"]
    };
  },
  methods: {
    addEmployee() {
      db.collection("employees")
        .add({
          employee_id: this.employee_id,
          dept: this.dept,
          first_name: this.first_name,
          last_name: this.last_name,
          position: this.position,
          ["Wage per Hr"]: this.wage,
          Cashapp: this.cashapp,
          email: this.email,
          phone: this.phone
        })
        .then(() => {
          alert("New Employee has been added");
          this.$router.push("/");
        })
        .catch(error => console.log(error));
    },
    checkInputs() {
      console.log(typeof this.dept, this.dept);
      if (this.employee_id.length < 3) {
        alert("Your ID needs at least 3 digits");
        return;
      } else if (
        !this.dept ||
        !this.first_name ||
        !this.last_name ||
        !this.position ||
        !this.wage ||
        !this.cashapp ||
        !this.email ||
        !this.phone
      ) {
        alert("Fill in Dept, First & Last Name, Position, etc");
      } else {
        this.addEmployee();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.cancel {
  background: red;
  color: white;
  border-radius: 5px;
}
.submit {
  background: green;
  color: white;
  border-radius: 5px;
  margin: 5px;
}
</style>
