<template lang="pug">
v-container(class="fill-height" fluid)
  v-row(align="center" justify="center")
    v-col(cols="12" sm="8" md="4")
      v-card(class="elevation-12")
        v-toolbar(dark flat)
          v-toolbar-title Login
          v-spacer
        v-card-text
          v-form
            v-icon(home)
            v-text-field(label="Email" name="Email" v-model="email" prepend-icon="mdi-email" type="text")
            v-text-field(label="Password" name="password" v-model="password" prepend-icon="mdi-lock" :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'" :type="showPassword ? 'text' :'password'" @click:append="showPassword = !showPassword")
        v-card-actions
          v-spacer
            v-btn(color="success" @click="submit") Login
</template>

<script>
import firebase from "firebase";

export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      showPassword: false
    };
  },
  methods: {
    submit(e) {
      e.preventDefault();
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(AuthenticatorResponse => {
          let user = AuthenticatorResponse.user;
          alert(`You are now signed in as ${user.email}`);
          this.$router.push("/");
        })
        .catch(error => {
          let errorCode = error.code;
          let errorMessage = error.message;
          if (errorCode === "auth/wrong-password") {
            alert("Wrong password.");
          } else {
            alert(errorMessage);
          }
        });
    }
  }
};
</script>

